#home{
    width: 90%;
    padding: 1rem;
    overflow: hidden;
}
#home-block{
    display: flex;
    flex-direction: column;
    margin: 0.5rem;
    color: whitesmoke;
    z-index: -1;
    width: 100%;
    height: 100%;
}

@media screen and (max-width:749px){
    #home{
        width: 85%;
    }
}

@media screen and (max-width:670px){
    #home{
        flex: 2;
        width: 80%;
        padding: 0rem;
    }
}

@media screen and (max-width:426px){
    #home-block{
        margin: 0.3rem;
    }
}
